.header {
    display: flex;
    background-color: $black;
    overflow: visible;
}

.page-header__nav.container {
    display: flex;
    color: $white;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin: 0 auto;
    transition: 0.4s all ease;
    overflow: visible;
}

.page-header__bar {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 10px;
}

.page-header__logo {
    width: 110px;
    height: 37px;
    margin: 0 auto;
}

.navigation-list {
    display: flex;
    align-items: center;
    position: relative;
}

.navigation-list__item {
    display: flex;
    align-items: center;
    justify-content: center;

    &:last-child {
        border-right: none;
    }

    &:hover {
        .navigation-list__item-link {
            color: $black;
        }
        background-color: $light-blue;
    }

    &:active {
        .navigation-list__item-link {
            color: $black;
        }
        background-color: $light-blue;
    }
}

.navigation-item-parent {
    position: relative;
}

.navigation-children-list {
    display: none;
    width: auto;
    position: absolute;
    bottom: -65px;
    left: -1px;
    z-index: 2000;
    background-color: $black;
    width: 320px;
}

.navigation-children-list-item {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    text-align: center;

    &:hover {
        .navigation-list__item-link {
            color: $black;
        }
        background-color: $light-blue;
    }
}

.navigation-item-parent:hover .navigation-children-list {
    display: flex;
}

.navigation-children-list-item__link {
    color: $white;
    @include fontface-semibold;
    font-size: 16px;
    line-height: 24.8px;
    text-decoration: none;
    padding: 20px 0;
    width: 100%;
    height: 100%;
}

.navigation-list__item-link {
    @include fontface-semibold;
    font-size: 16px;
    line-height: 24.8px;
    color: $white;
    text-decoration: none;
    padding: 40px 25px;
}

.current-item {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $black;
    background-color: $light-blue;
}

.header.fixed {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 100;
}